import axios from 'axios';
import index from '../router';

// axios 
axios.defaults.timeout = 8000;
axios.defaults.baseURL = 'https://39.106.57.246:4396';

// http request 
axios.interceptors.request.use(
  config => {
    config.headers.Authorization = null;
    if (localStorage.token && !(localStorage.token === "undefined")) { //token
      console.log(typeof localStorage.token);
      if (localStorage.token === 'wallet') {
        config.headers.Authorization = null;
        return config;
      }
      config.headers.Authorization = "Bearer " + localStorage.token;  //token
      console.log(config.headers.Authorization );
    }
    return config;
  },
  err => {
    return Promise.reject(err);
  }
);

// http response 
axios.interceptors.response.use(
  response => {
    if (response.data.errno === 999) {
      index.replace('/');
      console.log("token");
    }
    return response;
  },
  error => {
    return Promise.reject(error);
  }
);
export default axios;
